.tour-catalog__wrapper {
  @include vp-1023 {
    max-width: 1160px;
    margin: 0 auto;
    padding-top: 70px;
    padding-bottom: 120px;
    padding-left: 0;
    padding-right: 0;
  }

  @include vp-767 {
    width: auto;
    padding-top: 100px;
    padding-bottom: 100px;
    padding-left: 60px;
    padding-right: 60px;
  }

  @include vp-319 {
    width: auto;
    padding-top: 65px;
    padding-bottom: 65px;
    padding-left: 25px;
    padding-right: 25px;
  }
}

.tour-catalog__title {
  margin: 0 auto;
  font-weight: 600;
  word-wrap: break-word;

  @include vp-1023 {
    font-size: 28px;
    line-height: 42px;
    width: 302px;
    margin-bottom: 60px;
  }

  @include vp-767 {
    font-size: 26px;
    line-height: 39px;
    width: 281px;
    margin-bottom: 60px;
  }

  @include vp-319 {
    font-size: 24px;
    line-height: 36px;
    width: 259px;
    margin-bottom: 40px;
  }
}

.tour-catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;

  @include vp-1023 {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 30px;
    row-gap: 30px;
  }

  @include vp-767 {
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    row-gap: 30px;
  }

  @include vp-319 {
    grid-template-columns: 1fr;
    column-gap: 30px;
    row-gap: 30px;
  }
}

.tour-catalog__item {
