  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 478px;
  width: 260px;
  box-sizing: border-box;


  &:hover .tour-catalog__item-options {
    display: flex;
    flex-direction: column;
  }

  &:focus-visible .tour-catalog__item-options {
    display: flex;
    flex-direction: column;
  }

  &:hover .tour-catalog__link {
    display: block;
  }

  &:focus-visible .tour-catalog__link {
    display: block;
  }

  &:hover .tour-catalog__item-name {
    margin-bottom: 25px;
